
import Tile from "./Tile.vue";
import model from "./model.js";
import { ref, reactive } from "vue";

export default {
  name: "Board",
  components: {
    Tile
  },
  props: {
    msg: String,
    imageSrc: String
  },
  setup(props) {
    const game = reactive(model.game);
    const componentKey = ref(false);
    const onClick = i => {
      if (model.game.slide(i)) {
        componentKey.value = !componentKey.value;
      }
      console.log(
        "Move: " +
          i +
          ", count: " +
          model.game.count +
          ", board: " +
          game.board.join()
      );
    };
    const refresh = () => {
      componentKey.value = !componentKey.value;
    };
    return {
      game,
      componentKey,
      onClick,
      refresh
    };
  }
};
